import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { Subject } from 'rxjs/Subject';
import { ButtonDirective } from "./button.directive";

/**
 * @hidden
 */
@Injectable()
export class KendoButtonService {
    public buttonClicked: Subject<ButtonDirective> = new Subject<ButtonDirective>();
    public buttonClicked$: Observable<ButtonDirective> = this.buttonClicked.asObservable();
    public click(button: ButtonDirective): void {
        this.buttonClicked.next(button);
    }
}



